* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: "Poppins", sans-serif;
}

body {
  background-color: #f8f9fa;
  color: #333;
}

.container {
  overflow-y: hidden;
  height: 100vh;
}

/* MAIN NAVIGATION */

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  box-shadow: 0 0.2rem 0.8rem 0.1rem rgb(0, 0, 0, 0.2);
  padding: 0 2rem;

  position: sticky;

  top: 0;
  left: 0;
}

.main-nav {
  font-size: 2.2rem;
}

.logo-container {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.logo {
  display: flex;
  align-items: center;
  gap: 2.4rem;
  padding: 2.4rem 0;

  font-size: 2.2rem;
  font-weight: 600;
}

.logo-img {
  height: 6rem;
  width: 6rem;
}

.main-nav-link {
  text-decoration: none;
  color: #333;
  font-weight: 600;
  letter-spacing: 0.05rem;
}

.teamcode-form {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1.2rem;
}

.input-teamcode {
  font-size: 1.6rem;
}

.teamcode-label {
  font-size: 1.6rem;
  font-weight: 600;
  text-transform: uppercase;
}

.btn-teamcode {
  height: 3.2rem;
  width: 10rem;
  font-size: 1.8rem;
}

/* .btn-teamcode:hover {
  background: rgba(105, 188, 219, 1);
  cursor: pointer;
} */

.btn-teamcode-help {
  height: 3rem;
  width: 3rem;
  padding: 1rem;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  font-size: 1.8rem;
  font-weight: 600;
}

.page-content-container {
  display: flex;
  height: 100%;
  /* overflow-y: scroll; */
}

.login-form-container,
.help-modal {
  position: absolute;
  top: 50%;
  left: 50%;

  transform: translate(-50%, -50%);

  z-index: 999;
  width: 70%;
  height: 70%;
  border-radius: 1rem;

  background-image: radial-gradient(
    100% 100% at 100% 0,
    #5adaff 0,
    #5468ff 100%
  );

  color: #fff;

  /* text-align: left; */
  gap: 1.2rem;

  box-shadow: 0 1rem 3rem 1rem rgb(0, 0, 0, 0.2);
}

.login-form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.help-modal {
  padding: 4rem;
  gap: 1.2rem;
}

.help-title {
  font-size: 4.4rem;
}

.help-text {
  font-size: 1.8rem;
  margin-bottom: 1.2rem;
}

.btn-close-help {
  font-size: 1.8rem;
  font-weight: 500;
  width: 10rem;
  height: 4rem;
  align-self: center;
}

.login-title {
  font-size: 2.4rem;
  text-transform: uppercase;
  margin-bottom: 3.2rem;
}

.input-username,
.input-password {
  /* max-width: 12rem; */
  /* margin-right: 1.2rem; */
  font-size: 1.8rem;
  max-width: 30rem;
  height: 2.4rem;
  align-self: center;
  text-align: center;
  width: 80%;
}

.form-login {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 30rem;
  gap: 1.2rem;
}

.form-login,
.cta-signup,
.cta-signin {
  font-size: 1.4rem;
  font-weight: 600;
  margin-bottom: 1rem;
}

.login-label {
  font-size: 1.8rem;
  margin-right: 0.5rem;
  text-align: center;
}

.cta-signup,
.cta-signin {
  font-size: 1.8rem;

  height: 4.8rem;
  width: 10rem;
}

/* ASIDE */

.main-aside {
  width: 35rem;
  background-color: #f4f5f7;
  height: 100%;

  padding: 2rem;
  border-right: 0.2rem solid rgb(51, 51, 51, 0.1);
  overflow-y: scroll;
}

.main-aside::-webkit-scrollbar {
  display: none;
}

.project-selected-view {
  display: flex;
  align-items: center;
  gap: 1.2rem;
  margin-bottom: 2.4rem;
}

.project-icon {
  width: 6rem;
  height: 6rem;
  border-radius: 1rem;
  /* background-color: #228be6; */
  background-image: radial-gradient(
    100% 100% at 100% 0,
    #5adaff 0,
    #5468ff 100%
  );
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.2rem;
  stroke: #fff;
}

.aside-project-title {
  font-size: 1.8rem;
  font-weight: 500;
}

.aside-project-type {
  color: #555;
  font-size: 1.4rem;
  font-weight: 400;
}

.aside-nav {
  font-size: 1.8rem;
}

.aside-nav-list {
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
}

.aside-nav-item,
.aside-nav-item-proj {
  padding: 0.4rem;
  border-radius: 1rem;
  transition: all 0.3s ease;
}

.aside-nav-item:hover,
.aside-nav-item-proj:hover {
  background-color: rgb(233, 233, 233);
  cursor: pointer;
}

.aside-nav-link,
.project-nav-link {
  text-decoration: none;
  color: #444;
}

.add-project-button,
.btn-projects-back,
.btn-add-bug,
.btn-projects-delete,
.btn-edit-bug {
  width: 18rem;
  height: 3.2rem;
  font-size: 1.8rem;
  margin-bottom: 2rem;
}

.btn-projects-back,
.btn-projects-delete {
  margin-bottom: 0;
}

/* ADD PROJECT */

.add-project-container {
  border: 1px solid #5468ff;
  padding: 1.2rem 0;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 1.2rem;
  margin-bottom: 8rem;
  gap: 1.2rem;
}

.add-project-title {
  font-size: 1.6rem;
}

/* .add-project-button:hover,
.btn-projects-back:hover,
.btn-add-bug:hover {
  background: rgba(105, 188, 219, 1);
  cursor: pointer;
} */

/* MAIN CONTENT PANEL */

.section-main {
  padding: 2.4rem;
  width: 100%;

  transition: all 0.3s ease;
}

.text-path {
  font-size: 1.6rem;
  font-weight: 400;
  color: #555;
}

.content-heading {
  font-size: 3rem;
  color: #5468ff;
  margin-bottom: 1.6rem;
}

/* BUGS LIST HEADER */

.bugs-list-header {
  width: auto;
  border-bottom: 0.1rem solid rgb(51, 51, 51, 0.5);
  margin-bottom: 1.2rem;
}

.bugs-list {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr 1fr 0.5fr;
  align-items: center;
  /* justify-items: center; */
  list-style: none;

  transition: all 0.3s ease;
  margin-bottom: 1.2rem;
}

.bugs-header-list-item {
  font-size: 1.8rem;
  font-weight: 600;
}

.main-cta {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.search-field {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 2rem;
}

.search-icon {
  height: 2.4rem;
  width: 2.4rem;
  stroke: #333;
}

.input-search {
  height: 2.4rem;
}

.btn-search {
  height: 3.2rem;
  width: 10rem;
  font-size: 1.8rem;
}

/* USER BUG LIST */

.bugs-list--user {
  font-size: 1.6rem;
  margin-top: 1rem;
  padding: 0.8rem;
  border-radius: 1rem;
}

.status-icon {
  height: 2.4rem;
  width: 2.4rem;
}

.bugs-list--user:hover {
  background-color: rgb(233, 233, 233);
  cursor: pointer;
}

.bug-modal {
  position: absolute;
  top: 50%;
  left: 50%;

  transform: translate(-50%, -50%);

  z-index: 999;
  width: 70%;
  height: 70%;
  border-radius: 1rem;

  /* background-image: radial-gradient(
    100% 100% at 100% 0,
    #5adaff 0,
    #5468ff 100%
  ); */
  background-color: #5468ff;
  color: #fff;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1.2rem;

  transition: all 0.3s ease;
}

/* .bug-modal-container {
} */

.bug-modal-title {
  font-size: 2.4rem;
  text-transform: uppercase;
  text-align: center;
}

.secondary-text {
  padding-bottom: 2rem;
  font-size: 1.4rem;
  text-align: center;
}

.bug-modal-label {
  font-size: 2rem;
  font-weight: 600;
  letter-spacing: 0.05rem;
  margin-bottom: 1rem;
}

.bug-modal-input {
  width: 30rem;
  height: 3.4rem;
  border-radius: 1rem;
  padding: 0.8rem;
  border: none;
  overflow-x: scroll;
  font-size: 1.4rem;
  font-family: inherit;
  font-weight: 400;
}

.new-bug-form {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.4rem;
  text-align: center;
  margin-bottom: 1.2rem;
}

.bug-input-field {
  display: flex;
  flex-direction: column;
  font-family: inherit;
  font-size: 1.4rem;
  margin: 0 auto;
}

.input-bug-desc,
.input-bug-tags {
  height: 10rem;
  overflow-x: scroll;
  resize: none;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  font-family: inherit;
  font-weight: 400;
  font-size: 1.4rem;
}

.btn-cancel-add-bug,
.btn-submit-bug,
.btn-update-bug {
  font-size: 1.8rem;
  height: 4.8rem;
  width: 10rem;
}

.new-bug-btns {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.6rem;
  padding-top: 1.2rem;
}

.btn-bug--delete {
  height: 2.4rem;
}

.selected-bug {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  gap: 2rem;
  background-image: radial-gradient(
    100% 100% at 100% 0,
    #5adaff 0,
    #5468ff 100%
  );
  padding: 2rem;
  border-radius: 2rem;
}

.selected-bug-title {
  font-size: 3.2rem;
  color: #fff;
}

.selected-bug-data {
  color: #fff;
  font-size: 1.8rem;
  font-weight: 500;
}

.bug-edit-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  padding: 1.2rem;
}

